import Endpoint from '@/services/Endpoint'

export default {
  cardTypes (payload) {
    return Endpoint().get('/s1/card/cardType', {
      params: payload
    })
  },
  createCardType (payload) {
    return Endpoint().post('/s1/card/cardType', payload)
  },
  modifyCardType (payload) {
    return Endpoint().patch('/s1/card/cardType', payload)
  }
}
